@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap');

.outerCont {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 95%;
    height: 100%;
    border-radius: 20px;
    /* display: flex; */
    flex-direction: column;
    background-color: #0c1631;
}
.sidebarOne
{
    display: flex;
    margin-bottom: 20px;
}
.profilePic
{
    border-radius: 50%;
    height: 50px;
    width: 50px;
}
.container
{
     background-color: #0a2458;
     width: 95%;
     height: 100%;
     margin-left: 25px;
     border-radius: 20px;
     display: flex;
  flex-direction: column;

}
.message
{
    color: white;
    /* margin-left: 110px; */
    padding: 10px;
}
.title {
    margin-left: 1.5rem;
    margin-top: 1.5rem;
    font-family: 'Source Sans Pro', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 2rem;
    line-height: 15px;
    color: white;
    
}
.box1
{
    height: 10%;
}
.sorterCont {
    
    display: flex;
    align-items: center;
    margin-top: "1%";
    background-color: #0a2458;
}

.sorterText {

    font-family: 'Source Sans Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 1rem;
    line-height: 15px;
    color: white;
    width: fit-content;
    margin-left: 2rem;

}

.messageSorterCont {
    /* border: .5px solid #EEEEEE; */
    border: none;

}

.sorter {
    width: 100%;
    height: 100%;
    padding: .5rem;
    border: none;
    cursor: pointer;
    color: #2D9CDB;
    font-size: 1.1rem;
    font-family: 'Source Sans Pro', sans-serif;
    background:#0a2458;
}

.sorter:focus {
    outline: none;
    color: white;
    background-color: #0a2458;
}

.options {
    padding: .5rem !important;
    color: white;
}


@media screen and (min-width: 720px) and (max-width: 1440px){
    .outerCont {
      height: calc(100vh - 130px)
    }
  }

  @media (max-width:500px) {
    .outerCont{
        width: 98%;
    }
    
  }