/* ///pop-up/// */
.middlePopUp{
    position: absolute;
    width: 33.5%;
    height: 76%;
    z-index: 10;
    backdrop-filter: blur(10px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.middlePopUp div{
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 350px;
    color: white;
   background-color: #0C1631;
   text-align: center;
   gap: 20px;
   padding: 40px;
box-shadow: inset 1px 1px 24px #4CC9FF40;
border-bottom: 2px solid #FFF278;
border-radius: 15px;
position: relative;
    
   
}
.middlePopUp div p:first-of-type{
    color: #FFF278;
    font-size: 14px;
    font-weight: bolder;
}
.middlePopUp div p:nth-of-type(2){
    max-width: 200px;
    font-size: 10px;
}
.middlePopUp div p:last-child{
  position: absolute;
  right: 20px;
  top: 10px;
  font-weight: 100;
}

.middlePopUp div button{
    width: 210px;
    height: 40px;
    border-radius: 26px;
    border: none;
    background: linear-gradient(to left, #FFF278 ,#F8B533);
    font-weight: bolder;
}




  .fadedLeft {
    width: 32%;
    height: 80%;
    background-color: rgba(26, 30, 40, 1);
    border-radius: 10px;
    position: absolute;
    overflow: hidden;
    

    animation: targetTransformleft 1s forwards;
  }

@keyframes targetTransformleft {
    from {
      transform: translateX(0%) rotate(0deg);
      opacity: 1;
      
    }
    to {
      transform: translateX(-80%) rotate(-30deg) translateY(25%);
      opacity: 0;
     
    }
  }


 .fadedRight{
    width: 32%;
    height: 82%;
    background-color: rgba(26, 30, 40, 1);
    border-radius: 10px;
    position: absolute;
    z-index: 99999999;
    overflow: hidden;
   
     animation: targetTransformright 1s forwards;
 }

 @keyframes targetTransformright {
    from {
      transform: translateX(0%) rotate(0deg);
      opacity: 1;
      
    }
    to {
        transform: translateX(80%) rotate(30deg) translateY(25%);
      opacity: 0;
     
    }
  }